.reviews-container {
    padding: 20px;
}

.review-card {
    background-color: white;
    border-radius: 8px;
    padding: 15px;
    margin-bottom: 15px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    transition: transform 0.2s, box-shadow 0.3s; /* Smooth effects for card */
}

.review-card:hover {
    transform: translateY(-5px); /* Lift effect on hover */
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.2); /* Deeper shadow on hover */
}

.review-card h3 {
    margin-top: 0; /* Remove top margin */
}

.review-card p {
    margin-bottom: 5px; /* Add space below paragraphs */
}